<template>
	<div>
		<input type="file" name="" id="" value="" id="imgUrl"/>
		<div>{{msg}}</div>
		<div @click="">提交</div>
	</div>
</template>

<script type="text/javascript">
	export default {
		data() {
			return {
				imgUrl: '',
				list: [
					{
						id: 1,
						value: '阿萨'
					},
					{
						id: 1,
						value: '地方'
					},
					{
						id: 2,
						value: '请问'
					},
					{
						id: 1,
						value: '二人'
					}
				]
			}
		},
		computed: {
			msg() {
				let num = []
				let obj = {}
				for(let i = 0;i<this.list.length;i++) {
					if(!obj[this.list[i].id]){
		         		num.push(this.list[i]);
		         		obj[this.list[i].id] = true;
			      	}
				}
				return num
			}
		}
	}
</script>


